@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500&display=swap);
* {
  box-sizing: border-box;
  font-family: 'Rubik', sans-serif;
  background-color: hsl(226, 43%, 10%);
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
}
.control-panel {
    background-color: hsl(235, 46%, 20%);
    margin-top: 5rem;
    border-radius: 20px;
    width: 90%;
}

.control-panel__person {
    color: white;
    display: grid;
    grid-template-columns: 8rem 1fr;
    grid-template-rows: repeat(2, minmax(0, 1fr));
    background-color: hsl(246, 80%, 60%);
    border-radius: 20px 20px 20px 20px;
    height: 8rem;
}

.control-panel__person img {
    justify-self: center;
    align-self: center;
    grid-area: 1/1/3/2;
    border: 3px solid white;
    border-radius: 50%;
    background-color: transparent;
    height: 5rem !important;
}

.control-panel__person p {
    justify-self: flex-start;
    align-self: flex-end;
    grid-area: 1/2/2/3;
    background-color: transparent;
    padding-bottom: 0.5rem;
    font-weight: 300;
    color: rgba(255, 255, 255, 0.514);
}

.control-panel__person h3 {
    padding-top: 0.5rem;
    grid-area: 2/2/3/3;
    background-color: transparent;
    font-weight: 300;
    font-size: 1.2rem;
}

.control-panel__panel {
    background-color: transparent;
    min-height: 4rem;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
}

.control-panel__person img {
    justify-self: center;
    align-self: center;
    grid-area: 1/1/3/2;
    border: 3px solid white;
    border-radius: 50%;
    background-color: transparent;
    height: 70%;
}

.control-panel__person p {
    justify-self: flex-start;
    align-self: flex-end;
    grid-area: 1/2/2/3;
    background-color: transparent;
    padding-bottom: 0.5rem;
    font-weight: 300;
    color: rgba(255, 255, 255, 0.514);
}

.control-panel__person h3 {
    padding-top: 0.5rem;
    grid-area: 2/2/3/3;
    background-color: transparent;
    font-weight: 300;
    font-size: 1.2rem;
}

.control-panel__panel button {
    color: hsl(235, 45%, 61%);
    border: none;
    background-color: transparent;
    transition: 0.5s;
    font-size: 1.2rem;
}

.control-panel__panel button:hover {
    transition-timing-function: ease-out;
    color: white;
}

@media (min-width: 700px) {
    .control-panel {
        width: 35%;
        margin: 0 0 0 0.5rem;
        height: 25rem;
    }

    .control-panel__person {
        grid-template-columns: 1fr;
        grid-template-rows: 8rem 2rem 1fr;
        padding-left: 2rem;
        padding-right: 2rem;
        height: 80%;
    }

    .control-panel__person img {
        grid-area: 1/1/2/2;
        justify-self: flex-start;
    }

    .control-panel__person p {
        grid-area: 2/1/3/2;
        font-size: 0.8rem;

    }

    .control-panel__person h3 {
        grid-area: 3/1/4/2;
        font-size: 1.8rem;
    }

    .control-panel__panel {
        flex-direction: column;
        align-items: flex-start;
        padding-left: 2rem;
        height: 5rem;
    }

    .control-panel__panel button {
        font-size: 0.8rem;
    }
}

@media (min-width: 850px) {
    .control-panel {
        width: 15rem;
    }
}
.activities {
    width: 100%;
    height: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
}

.activities__holder {
    width: 90%;
    margin-top: 2rem;
}

.activities__holder-all {
    height: auto;
    border-radius: 15px 15px 20px 20px;
    padding-top: 3rem;
    background-repeat: no-repeat;
    background-size: 80px;
    background-position: right top;
}

.activities__holder-data {
    background-color: hsl(235, 46%, 20%);
    display: grid;
    align-items: center;
    grid-template-columns: 8rem 1fr;
    grid-template-rows: repeat(2, minmax(0, 1fr));
    height: 10rem;
    width: 100%;
    border-radius: 15px;
}

.activities__holder-data p {
    background-color: transparent;
    color: white;
    padding-left: 2rem;
    padding-right: 2rem;
}

.activities__holder-data p:nth-child(1) {
    grid-area: 1/1/2/2;
    text-align: left;
    font-weight: 500;
}

.activities__holder-data p:nth-child(2) {
    grid-area: 1/2/2/3;
    text-align: right;
    font-size: 2rem;
}

.activities__holder-data p:nth-child(3) {
    grid-area: 2/1/3/2;
    text-align: left;
    font-size: 2rem;
    font-weight: 300;
    align-self: flex-start;
}

.activities__holder-data p:nth-child(4) {
    grid-area: 2/2/3/3;
    text-align: center;
    font-weight: 400;
    font-size: 1rem;
    color: hsl(235, 36%, 69%);
    align-self: flex-start;
    padding-top: 0.8rem;
}

.activities__holder-Work {
    background-color: rgb(255, 139, 100);
    background-image: url(/Time_Tracking_Dashboard/static/media/icon-work.a3f124e5.svg);

}

.activities__holder-Play {
    background-color: rgb(86, 194, 230);
    background-image: url(/Time_Tracking_Dashboard/static/media/icon-play.e8d4e569.svg);
}

.activities__holder-Study {
    background-color: rgb(255, 94, 125);
    background-image: url(/Time_Tracking_Dashboard/static/media/icon-study.1f0a65e4.svg);
}

.activities__holder-Exercise {
    background-color: rgb(75, 207, 131);
    background-image: url(/Time_Tracking_Dashboard/static/media/icon-exercise.3e7b0282.svg);
}

.activities__holder-Social {
    background-color: rgb(114, 53, 209);
    background-image: url(/Time_Tracking_Dashboard/static/media/icon-social.64be0c18.svg);
}

.activities__holder-SelfCare {
    background-color: rgb(241, 199, 91);
    background-image: url(/Time_Tracking_Dashboard/static/media/icon-self-care.a161aff5.svg);
}

@media (min-width: 700px) {

    .activities {
        height: 26rem;
    }

    .activities__holder {
        width: 30%;
        margin: 0;
    }

    .activities__holder-all {
        padding-top: 2rem;
        background-size: 50px;
    }

    .activities__holder-data {
        grid-template-columns: 6rem 3.8rem;
        grid-template-rows: repeat(3, minmax(0, 1fr));
        height: 10rem;
    }

    .activities__holder-data p {
        margin: 0;
        margin-left: 0.5rem;
        padding: 0;
    }

    .activities__holder-data p:nth-child(1) {
        font-size: 0.8rem;
        grid-area: 1/1/2/2;
    }

    .activities__holder-data p:nth-child(2) {
        grid-area: 1/2/4/3;
        justify-self: flex-start;
        align-self: flex-start;
    }

    .activities__holder-data p:nth-child(3) {
        grid-area: 2/1/3/2;
        align-self: center;
    }

    .activities__holder-data p:nth-child(4) {
        grid-area: 3/1/4/2;
        font-size: 0.7rem;
        width: 5rem;
        padding: 0;
        text-align: left;
        align-self: flex-start;
    }
}
.App {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: auto;
    width: 100vw;
}

@media (min-width: 700px) {
    .App {
        flex-direction: row;
        height: 100vh;
    }
}

@media (min-width: 850px) {
    .App {
        width: 53rem;
    }
}
